* {
    margin: 0%;
    padding: 0%;
}

.container {
    width: 100%;
    height: 1200px;
    background-color:#a8dadc;
    max-width: 1200px;
    min-width: 400px;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
}
.line{
    width: 100%;
   
    background-color: #03071e;
    border: #03071e;
    height: 5px;
}
.Stick {
    background-color:lightskyblue;
    color: #222A36;
    opacity: .9;
    box-shadow: 0 3px 10px #2c2c2c;
    transition: all .2s ease-in;
}
.Stick .Option{
    color:#222a36
}

header {
    width: 100%;
    max-width: 1200px;
    height: 70px;
    display: flex;
    align-items: center;

    background-color: #222A36;
    color: aliceblue;
    

    position: fixed;
    top: 0;
}

.Option {
    display: flex;
    justify-content: space-evenly;
    width: 60%;
    font-size: 1.1rem;
    color:ghostwhite;
}

.dText {
    width: 50%;

    margin-left: 2%;

}

.opt:hover {
    color: lightcoral;

}

.Sec2 {
    width: inherit;
    max-width: inherit;
    background-color: #222A36;

    display: flex;
    justify-content: space-between;
    color: antiquewhite;
    margin-top: 3%;
    height: 500px;
}

.Heading {
    color: white;

    margin-left: 50px;
    font-size: 2.5rem;
    margin-top: 100px;
    width: 35%;

}

.DpI {
    color: white;

    font-size: 3rem;
    width: 45%;
    height: inherit;
    margin-right: 40px;
}

.F1 {
    color: #6FC5D8;
}

.Im1 {
    width: 100%;
    height: 450px;
    margin-top: 50px;

}

.PrHeading {
    width: 100%;
    display: flex;
    text-align: center;



    box-sizing: border-box;

}

.PrHeading h1 {

    margin-left: 70px;
    background-color: red;

}

.TxtHead {
    font-size: 2.5rem;
}

.PrContent {

    margin-top: 30px;
    height: 300px;
    background-color: red;
    display: flex;
    padding-top: 50px;
    padding-bottom: 50px;

}

.aboutCon {
    display: flex;
    flex-direction: column;
    opacity: .98;
    background-image: url("https://assets.weforum.org/article/image/vjTtA2jEsRoWHWuYUlkwxrkq-bdnieCej1ICtznYeA4.jpg");
    background-size: cover;
    background-position: center center;
    font-family: "Josefin Sans";
    font-size:3rem;
    color:white;

}

.aboutHead {
    display: flex;
    justify-content: center;
}

.aboutText {
    display: flex;
    margin-left: auto;
    margin-right: 10%;
    text-align: center;
 
    
    width: 50%;
}
.burer-menu{
    display: none;
}















.wrapperSki{
    width: 100%;
   
    
}
.containerSki{
    width: 100%;
    padding: 30px 0px 50px;
    background-color: #9fc9eb;
    box-shadow: inset 0 3px 10px #222a36;
   
} 
.containerSki *{
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    color: black;
    font-weight: 500;
}
.h2Ski{
    
    margin-bottom: 50px;
    letter-spacing: 2px;
    margin-left: 10%;
    font-size: 4rem;
    font-weight: bold;
    background: linear-gradient(90deg,#222A36,#9fc9eb,#818a96);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  
    
    
 
}

.skills{
    display: flex;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    
}
.Skils2{
    width: 92%;
     
    margin-bottom: 10px;
  
}
.vfx{
    display: flex;
    flex-direction: column;
    
   
}

.details{
    
    width: 80%;
    display: flex;  
    justify-content: space-between;
    margin-bottom: 10px;
}
.bar{
    position: relative;
 
    border: 2px solid #0d96e0;
 
    border-radius: 20px;
  
}
.bar div{
    position: relative;
    width: 0;
    height: 9px;
    border-radius: 10px;
    background-color: #2c2c2c;
   
 
}
#html-bar{
    animation: html-fill 2s forwards;
}
@keyframes html-fill{
    100%{
        width: 90%;
    }
}
#css-bar{
    animation: css-fill 2s forwards;
}

@keyframes css-fill{
    100%{
        width: 80%;
    }
}

#js-bar{
    animation: js-fill 2s forwards;
}
@keyframes js-fill {
    100%{
        width: 80%;
    }
}

#rea-bar{
    animation: rea 2s forwards;
}
@keyframes rea{
    100%{
        width: 75%;
    }
} 
#red-bar{
    animation: red 2s forwards;
}
@keyframes red{
    100%{
        width: 65%;
    }
}
#boo-bar{
    animation: boo 2s forwards;
}
@keyframes boo{
    100%{
        width: 80%;
    }
}
#tai-bar{
    animation: tai 2s forwards;
}
@keyframes tai{
    100%{
        width: 70%;
    }
}

.circle {
    border-radius: 50px;
    width: 50px;
    height: 50px;
    background-color: #0d96e0;
  }

  .ProjectMH{
    
    font-size: 2.5rem;
    padding: 20px;
    display: flex;
    justify-content: center;
    background: linear-gradient(90deg,#222a36,#222a36,#818a96);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;

  }
  .MainPro{
    background-color:#818a96;
    box-shadow: inset 0 3px 10px #222a36;
  }
  .ProjectCon{
    padding-top: 30px;
    width: 100%;
    padding-bottom: 50px;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    font-size: 2rem;
    color: #a8dadc;
}
.ProjectScreen{
    width: 100%;
    height: 450px;
    margin-left: auto;
    margin-right: auto;
    
}
.fn{
    font-size: 1rem;
}
.textP{
   font-size: 1.6rem;
   display: flex;
   flex-direction: column;
   justify-content:flex-start;
   align-items: center;
   color: white;
}
.ProjectDs{
    padding-bottom: 15px;
}
.aboutHead{
    background: linear-gradient(90deg,darkcyan,#9fc9eb,darkcyan,#9fc9eb,#818a96);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}























