        font-size: 1.5rem;
        padding: 10px;
    
      }
    .Sec2 {
        height: 400px;
    }

    .Im1 {
        height: 360px;
        margin-top: 40px;

    }

    .Heading {
        font-size: 1.5rem;
        margin-top: 130px;
    }
    .aboutCon {
        
        font-size:2rem;
    
    }

    
    .ProjectCon{
        font-size: 1.5rem;
        height: 600px;
    }
    .textP{
        font-size: 1.4rem;
    }
    .fn{
        font-size: .8rem;
    }
    .h2Ski{
    
        
        margin-left: auto;
        margin-right: auto;
        font-size: 3rem;
    }

}

@media (max-width:600px) {
    .Sec2 {
