
.ballo{

 display: flex;
 flex-direction: column;


}
.ball{
    width: 100px;
    
    height: 100px;  
    border-radius:50% ;
}
.arr{
    width: 900px;
}
.AniCon{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
}
.AnImage{
    border: 15px solid burlywood;
    width: 200px;
    height: 200px;
    animation-name: image;
    animation-duration: 10s;
    animation-delay: 5s;
  
}
.Hed{
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
@keyframes image{
    from{
        border-radius: 100%;
        transform:rotate(360deg);
    }
   }

    .b1{
       
        width: 100px;
        height: 100px;
        animation-name: b1;
        animation-duration: 5s;
       
       }
       @keyframes b1{
        from{
            transform: translate(-400%,-200px);
        }
       }
       .b2{
       
        width: 100px;
        height: 100px;
        animation-name: b2;
        animation-duration: 5s;
       
       }
       @keyframes b2{
        from{
            transform: translate(-400%,200px);
        }
       }
       .b3{
       
        width: 100px;
        height: 100px;
        animation-name: b3;
        animation-duration: 5s;
       
       }
       @keyframes b3{
        from{
            transform: translate(400%,-200px);
        }
       }

       .b4{
       
        width: 100px;
        height: 100px;
        animation-name: b4;
        animation-duration: 5s;
       
       }
       @keyframes b4{
        from{
            transform: translate(400%,200px);
        }
       }
